<template>
   <header class="header">
    <h1 class=" text-7xl ">To do list</h1>
  </header>

  <div class=" text-5xl p-10 space-y-10">
    <h2> {{ this.$store.state.username }}'s tasks </h2>
  </div>
    <button class="bg-transparent hover:bg-blue-500 text-white font-semibold hover:text-white py-2 px-4 border border-blue-500
     hover:border-transparent rounded" @click="change" key="change">
    <input class="  border-none text-black mr-3 focus:outline-none" placeholder="Change your name" type="text" v-model="changeName.username"  @keyup.enter="change">
      OK
    </button>
    <br>
    <br>
    <br>
  <div class="table border-separate">
    <table>
      <ul>
        <li v-for="(tarefa, id) in this.$store.state.tasks" :key="tarefa" ref="id">
          <div class="effects">
            <div class="flex items-center justify-between gap-2">
              <template v-if="!line">

                <span class="task">{{ tarefa }}</span>

              </template>

              <template v-else>

                <span class="task active">{{ tarefa }}</span>

              </template>

              <div class="flex items-center cursor-pointer">

              <svg  class="w-12 h-12 svg" viewBox="0 0 16 16" @click="methodLine()" fill="black" xmlns="http://www.w3.org/2000/svg">
                <path d="M4 8a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7A.5.5 0 0 1 4 8z"/>
                </svg>
                <svg class=" cursor-pointer svg" width="20" height="20" @click="del(id)" viewBox="0 0 90 90" xmlns="http://www.w3.org/2000/svg">
                <path d="M45 0C20.1 0 0 20.1 0 45C0 69.9 20.1 90 45 90C69.9 90 90 69.9 90 45C90 20.1 69.9 0 45 0ZM66.3 59.9C67 60.6 67 61.6
                66.3 62.3L62.2 66.4C61.5 67.1 60.5 67.1 59.8 66.4L45 51.4L30.1 66.3C29.4 67 28.4 67 27.7 66.3L23.6 62.2C22.9 61.5 22.9 60.5
                23.6 59.8L38.6 45L23.7 30.1C23 29.4 23 28.4 23.7 27.7L27.8 23.6C28.5 22.9 29.5 22.9 30.2 23.6L45 38.6L59.9 23.7C60.6 23 61.6
                23 62.3 23.7L66.4 27.8C67.1 28.5 67.1 29.5 66.4 30.2L51.4 45L66.3 59.9Z" fill="black"/>
              </svg>
              </div>
            </div>


          </div>
        </li>
      </ul>
    </table>
  </div>

  <div>
  <input class="border-none text-black mr-3 py-1 px-2 focus:outline-none w-50" type="text" placeholder="Write your task" @keyup.enter="add" ref="item.id" v-model="form.nome">

  <button class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-full" @click="add" key="Add">
    Add
  </button>

  </div>
</template>

    <script>
  import { mapState, Store, createStore } from 'vuex';
  import '../app.css';

  export default {
    data() {
      var compare = 0;
      return {
        line : false,
        form: {
          nome: '',
        },
        changeName: {
          username: '',
        }
      };
    },
    computed: {
      ...mapState(['username']),
    },
    methods: {
      methodLine(){
        this.line = !this.line
        // if(!this.line){
        //   this.$store.commit('saveData', this.line)
        // }
      },
      change() {
        if (this.changeName.username.length == 0) {
          return;
        }
        this.$store.commit('setUsername', this.changeName.username);
        this.changeName.username = '';
      },

      add() {
        if (this.form.nome.length == 0) {
          return;
        }
        this.$store.commit('addTask', this.form.nome);
        this.form.nome = '';
        },
      del(id) {
        this.$store.commit('removeTask', id);
      },
    },
  };
</script>
